* {
    border: none;
}

:root {
    --first-color: #191919;
    --second-color: #2D4263;
    --third-color: #C84B31;
    --fourth-color: #ECDBBA;
}

body {
    background-color: var(--fourth-color);
    color: var(--second-color);
}

.header {
    padding: 15px 20px;
    text-align: center;
    color: var(--third-color);
    background-color: var(--first-color);
}

.intro-div {
    text-align: center;  
    padding: 2px 10px;  
}

.intro-div h2 {
    font-size: larger;
    font-weight: normal;
}


nav {
    margin: 1px auto;
    padding: 10px;
    text-align: center;
    overflow: hidden;
    width: fit-content;
}

nav button, button {
    background-color: var(--first-color);
    color: var(--third-color);
    font-family: inherit;
    font-size: inherit;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    margin: 1px 5px;
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

nav button {
    margin: 1px 20px;
}

nav button:hover, button:hover {
    background-color: var(--second-color);
    color: var(--fourth-color);
}

button:disabled {
    background-color: var(--fourth-color);
    color: var(--second-color);
}

.btn-start {
    margin-top: 30vh;
    width: 50vw;
}

.started-button{
    margin: 50% 20px;
    width: 60vw;
    border-radius: 50px;
}

.ListContainer {
    padding: 1px 10%;
    text-align: center;
}

textarea,input {
    border: 1px solid var(--second-color);
    border-radius: 10px;
    color: var(--third-color);
    font-size: larger;
    margin: 10px auto 10px auto;
    padding: 5px;
}

.ListContainer textarea {
    width: 100%;
    height: 50vh;
}

.div-result {
    text-align: center;
}

.div-result textarea {
    height: 25vh;
}

textarea:focus, input:focus {
    border: 2px solid var(--third-color);
  }

input, textarea {
    display: block;
    font-family: inherit;
    font-size: inherit;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.errorMsg {
    padding: 5px;
    color: red;
    text-align: center;
}